  #roleActor {
    font-size: 2em;
    padding: 1vh;
    text-align: center;
  }
  #roleInner {
    display: flex;
    width: 225px;
    flex-direction: column;
    align-content: center;
    justify-content: center;
  }
  .roleGallery {
    width: 100%;
    display: flex;
    flex-direction: column;
    /* height: 30vw; */
    height: fit-content;
    align-self: center;
    align-items: center;
    justify-content: center;
    /* text-align: center; */
    padding: 20px 10px;
    padding-top: 0;
    border-bottom: 1px;
    /* padding-bottom: 0; */
  }
  .roleGallery img {
    width: 225px;
    height: 326px;
    margin: auto;
    align-self: center;
  }
  .roleTogglePrev {
    background-color: darkslategrey;
    width: 40px;
    height: 40px;
    text-align: center;
    justify-content: center;
    align-self:stretch;
    /* flex: none; */
    font-size: 1em;
    border-width: .3vh;
  }
  .roleToggleNext {
    background-color: darkslategrey;
    width: 40px;
    height: 40px;
    text-align: center;
    align-self:stretch;
    /* flex: none; */
    padding: 0;
    font-size: 1em;
    border-width: .3vh;
  }
  .imgNav {
    display: flex;
    /* flex-direction: row; */
    width: 100%;
    height: fit-content;
    /* justify-content: ; */
    padding: 1vw 0;
    /* justify-content: space-between; */
  }
  .selectionDots {
    flex: auto;
    font-size: 25px;
    text-align: center;
    /* max-width: 6vw; */
    flex-wrap: wrap;
    padding-left: .3vw;
    padding-right: .3vw;
  }
  .selectionDots .index {
