    font-size: 16px;
    display: flex;
    flex-direction: row-reverse;
    margin-right: 2vh;
  }
  .roleGallery h4 {
    color: #6598ea;
    /* font-size: 2em; */
    font-size: 4vh;
  }
  .roleGallery p {
