    margin: auto;
    display: flex;
    flex-direction: column;
    text-align: left;
    border-radius: 1em;
    background-color: #44444c;
    padding: 1em;
    padding-top: 0;
  }
  .showsList:hover {
    /* position: absolute; */
    margin: auto;
    display: flex;
    flex-direction: column;
    text-align: left;
    border-radius: 1em;
    background-color: #44444c;
    padding: 1em;
    padding-top: 0;
