  border-radius: 1vw;
  margin: .1vw;
  width: 100%;
  padding: 1vh;
  cursor: pointer;
  text-decoration: none;
}
  


  /* MOBILE NAV BAR COMPONENTS */ 
  .nav {
    position: -webkit-sticky; /* Safari */
    /* position: sticky; */
