    display: flex;
    justify-content: space-between;
    align-items: stretch;
    z-index: 10;
    height: 7vh;
    top: 0;
    border: .2vw solid #f2f2f2;
  }
  .nav a {
    /* margin-left: 16px; */
    align-items: center;
    text-decoration: none;
    font-size: 2vh;
    height: 100%;
