  }
  .nav li:hover {
    background-color: #777;
  }
  .nav a:hover {
    color: #f1356d;
  }
  #userSearchArea {
    display: flex;
    padding: 1vh;
    height: 4vh;
    align-items: stretch;
    justify-content: space-between;
  }
  #userSearch {
    position: relative;
    display: flex;
    z-index: 11;
    font-size: 2vh;
    color: #202025;
    /* height: 3vh; */
    /* top: .25vh; */
    padding: 0vw 0vw 0vw .75vh;
    border-width: .2vh;
    border-radius: 1vh;
  }
  #userSearchButton {
    position: relative;
    z-index: 11;
    font-size: 1.5vh;
    /* left: 60%; */
    background-color: rgb(105, 105, 105);  
    /* height: 3vh; */
    border-width: .3vw;
    border-radius: .5vh;
    padding: 0 1vw 0 1vw;
  }
  #filter {
    font-size: 1vh;
  }
  #unfilter {
    /* height: 2vh; */
    border-width: .1vh;
    font-size: 1vh;
    background-color: rgb(105, 105, 105);  
    padding: .1vh;
  }
  #filterLabel {
    gap: 0;
    /* position: fixed; */
    align-items: stretch;
    height: 3vw;
    /* left: 25%; */
    display: flex;
    z-index: 11;
    flex-direction: column;
  }


  /* MOBILE HOME SCREEN */

