    right: 5%;
    border-width: .1vw;
    padding: 1vh;
  }
  #nextPage:disabled {
    background-color: #202025;
  }

/* /////////// END ANIME /////////// */

/* MOBILE ACTOR PAGE */

.actorPage {
  position: relative;
  display: flex;
  /* justify-content: start; */
  gap: 25px;
  background-color: #202025;
  top: 0;
  /* min-height: 100vh; */
  height: 100%;
}  
.actorSearchSide {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: start;
  right: 0px;
  top: 15vh;
  z-index: 1;
}
#actorSearch {
  align-self: flex-end;
  top: 0;
  height: 5vh;
  width: 29vh;
  margin-right: 1vh;
  font-size: 3vh;
  border-width: .25vw;
  border-radius: 1vw;
  color: #202025;
  /* margin: 0 3vh; */
  padding-left: 1vw;
}
.actorInfo {
  position: sticky;
  display: flex;
  flex-direction: column;
  /* width: 25%; */
  /* left: 70% ; */
  top: 30%;
  font-size: 1.6vh;
  text-align: center;
  /* margin-left: -300px; */
  /* margin-left: auto;
  margin-right: auto; */
  align-items: center;
}
.actorImg {
  display: flex;
  align-self: center;
  width: 15vh;
}
.actorRoles {
  margin-top: 10vh;
  margin-bottom: 30vh;
  margin-left: 2vh;
  gap: 1vw;
  width: 70%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.actorRole {
  position: relative;
}
.actorRole img {
  width: 100%;
}
.info {
  display: none;
  margin: auto;
  padding: 10px;
  /* top: 23.3vh; */
  min-width: 30vh;
  border-radius: 1vh;
  z-index: 2;
  position: absolute;
  flex-direction: column;
  /* max-width: 40vw; */
}
.actorInfoTitle {
  padding: 10px;
  text-align: end;
}
.actorRole img:hover {
  box-shadow: 0vh 0vh 1vh .5vh  #f1356d;
}
.actorRole img:hover + .info {
  background-color: #f1356d;
  display: flex;
  box-shadow: 0vh 0vh 1vh .3vh  #53768d;
}
.info:hover {
  background-color: #53768d;
  display: flex;
  box-shadow: 0vh 0vh 1vh .3vh  #f1356d;
}


}



