  margin: 0 1vw;
  padding: 1vw;
}
.results {
  display: none
}
.search:focus + .results {
  margin: 0 1vw;
  right: 0px;
  top: 0px;
  display: flex;
  flex-direction: column;
  font-size: 1.75vw;
}
.results:hover {
  margin: 0 1vw;
  right: 0px;
  top: 0px;
  display: flex;
  flex-direction: column;
  font-size: 1.75vw;
}
.resBox {
  position: relative;
  text-align: left;
  right: 0;
  border: #f1356d ridge .25vw;
  border-radius: 1vw;
  margin: .1vw;
  width: 20vw;
  padding-left: 1vw;
  cursor: pointer;
  text-decoration: none;
}
.actorInfo {
  position: fixed;
  display: flex;
  flex-direction: column;
  width: 25%;
  left: 70% ;
  top: 30%;
  font-size: 1.6vh;
  text-align: center;
  /* margin-left: -300px; */
  /* margin-left: auto;
  margin-right: auto; */
  align-items: center;
}
.actorImg {
  display: flex;
  align-self: center;
  width: 15vh;
}
.actorRoles {
  margin-top: 1vh;
  margin-bottom: 30vh;
  margin-left: 2vh;
  gap: 1vw;
  width: 70%;
  display: flex;
  align-content: flex-start;
  flex-wrap: wrap;
}
.actorRole {
  height: 23.3vh;
  display: flex;
  flex-direction: column;
  position: relative;
}
.actorRoles img {
  width: 15vh;
  height: 23.3vh;
  /* width: 225px; */
  /* height: 350px; */
}
.info {
  display: none;
  margin: auto;
  padding: 10px;
  top: 23.3vh;
  min-width: 20vw;
  border-radius: 1vh;
  z-index: 2;
  position: absolute;
  flex-direction: column;
  /* max-width: 40vw; */
}
.actorInfoTitle {
  padding: 10px;
  text-align: end;
}
.actorRole img:hover {
  box-shadow: 0vh 0vh 1vh .5vh  #f1356d;
}
.actorRole img:hover + .info {
  background-color: #f1356d;
  display: flex;
  box-shadow: 0vh 0vh 1vh .3vh  #53768d;
  /* gap: 10px; */
  /* left: 15vh; */
}
.info:hover {
  background-color: #53768d;
  display: flex;
  box-shadow: 0vh 0vh 1vh .3vh  #f1356d;
  /* gap: 10px; */
  /* left: 15vh; */
}

